<template>
  <div class='clear-fix' style="padding:20px;">
    <Button type="primary" icon="ios-plus-empty" @click="handleCateAdd" style='margin-bottom:20px;'>
      添加分类
    </Button>
    <Table :loading="loading" border ref="catelist" :columns="cateColumns" :data="cateData" class='main-table'></Table>
  <div style="margin-top: 15px;overflow: hidden">
      <div style="float: right;">
        <Page :total="totalcount" :page-size="page.pagesize" :page-size-opts="[10,20,30,40]" show-elevator show-sizer
          placement="top" show-total @on-change='indexChange' @on-page-size-change='sizeChange'
          :current="page.pageindex+1"></Page>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        totalcount: 0,
        page: {
          pagesize: 10,
          pageindex: 0,
        },
        loading: false,
        cateColumns: [
          {
            type: 'index',
            width: 60,
            align: 'center',
          },
          {
            title: '分类名称',
            key: 'catename',
            align: 'center'
          },
          {
            title: '图片',
            key: 'catepic',
            width: 150,
            align: 'center',
            render: (h, params) => {
              return h('div', {
                attrs: {
                  style: 'width: 80px;height: 80px;display:inline-block;vertical-align:middle;'
                },
              }, [
                h('img', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  attrs: {
                    src: params.row.catepic,
                    style: 'width: 60px;height: 60px;border-radius: 2px;margin-top:10px;'
                  },
                  style: {},
                }),
              ]);
            }
          },
          {
            title: '首页推荐',
            key: 'isrecommend',
            width: 120,
            align: 'center',
            render: (h, params) => {
              return h('span', params.row.isrecommend == 1 ? '是' : '否')
            }
          },
          {
            title: '排序',
            key: 'sortvalue',
            width: 100,
            align: 'center'
          },
          {
            title: '操作',
            key: 'action',
            width: 130,
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.$router.push('/category/edit?cateid=' + params.row.id)
                    }
                  }
                }, '查看'),
                h('Button', {
                  props: {
                    type: 'warning',
                    size: 'small'
                  },
                  on: {
                    click: () => {
                      this.confirm('此操作将会永久删除该分类，请确认删除', '提醒').then(() => {
                        this.handleDelCate(params.row.id).then(() => {
                          this.getCategoryList()
                        }).catch(erro => {
                          this.$Message.error(erro)
                        })
                      }).catch(erro => {

                      })

                    }
                  }
                }, '删除')
              ]);
            }
          }
        ],
        cateData: []
      }
    },
    mounted() {
      this.$sitemap.prvname = "商品分类";
      this.$sitemap.currname = '分类列表';
      this.$menuactive.open = ['ItemBlock'];
      this.$menuactive.active = 'CategoryList';
      this.getCategoryList()
    },
    methods: {
      indexChange(index) {
        this.page.pageindex = index - 1;
      },
      sizeChange(size) {
        this.page.pagesize = size
      },
      getCategoryList() {
        this.$http.post(this.$api.category_list, this.page).then(res => {
          if (res.data.data) {
            this.cateData = res.data.data
            this.totalcount = +res.data.totalcount
          }
        }).catch(erro => {
        })
      },
      handleCateAdd() {
        this.$router.push(`/category/add?parentid=0&&parentname=根级分类`)
      },
      handleDelCate(cateid) {
        return this.$http.post(this.$api.category_del, {cateid,pagesize: 10,
          pageindex: 0,}).then(res => {
          if (res.data.result != '1') {
            return Promise.reject(res.data.msg)
          } else {
            this.$Message.success('删除成功')
          }

        })
      },
      confirm(text, title) {
        let that = this
        return new Promise((resolve, reject) => {
          that.$Modal.confirm({
            title: title ? title : '确认',
            content: text,
            onOk: () => {
              return resolve(true)
            },
            onCancel: () => {
              return reject(false)
            }
          })
        })
      },
    }
  }

</script>
<style>


</style>



